import React, { Component } from "react";
import { BrowserRouter, Route } from "react-router-dom";

import "./App.css";
import Home from "./components/Home/Home";
import Staff from "./components/Staff/staff_staff";
import CalculatorImage from "./components/calculator/calculatorImage";

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <div>
          <Route exact path="/sangria71" component={Home} />
          <Route path="sangria71/home" component={Home} />
          <Route path="sangria71/SplitList" component={Staff} />
          <Route path="sangria71/CalculatorImage" component={CalculatorImage} />
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
